import { Component, Inject, OnInit } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import icMoreVert from '@iconify/icons-ic/twotone-more-vert';
import icClose from '@iconify/icons-ic/twotone-close';
import icPrint from '@iconify/icons-ic/twotone-print';
import icDownload from '@iconify/icons-ic/twotone-cloud-download';
import icDelete from '@iconify/icons-ic/twotone-delete';
import icPhone from '@iconify/icons-ic/twotone-phone';
import icPerson from '@iconify/icons-ic/twotone-person';
import icMyLocation from '@iconify/icons-ic/twotone-my-location';
import icLocationCity from '@iconify/icons-ic/twotone-location-city';
import icEditLocation from '@iconify/icons-ic/twotone-edit-location';
import at from '@iconify/icons-fa-solid/at';
import { NgForm } from '@angular/forms';
import { AngularFirestore, AngularFirestoreCollection, AngularFirestoreDocument } from '@angular/fire/firestore';
import { firestore } from 'firebase';
import { StorageService } from '../../../../shared/services/storage/storage.service';
import { WareHouse } from 'src/app/shared/models/wareHouse';
import { WareHouseService } from 'src/app/shared/services/wareHouse.service';

declare var toastr: any;
declare var $: any;
@Component({
  selector: 'vex-almacen-create-update',
  templateUrl: './almacen-create-update.component.html',
  styleUrls: ['./almacen-create-update.component.scss']
})
export class AlmacenCreateUpdateComponent implements OnInit {

  almacenWeb: WareHouse = new WareHouse();
  
  loading = false;
  wareHosuehmentList: WareHouse[];
  wareHouseObject: WareHouse;

  static id = 100;

  form: FormGroup;
  mode: 'create' | 'update' = 'create';

  icMoreVert = icMoreVert;
  icClose = icClose;

  icPrint = icPrint;
  icDownload = icDownload;
  icDelete = icDelete;

  icPerson = icPerson;
  icMyLocation = icMyLocation;
  icLocationCity = icLocationCity;
  icEditLocation = icEditLocation;
  icPhone = icPhone;

  constructor(
    @Inject(MAT_DIALOG_DATA) 
    public defaults: any,
    private dialogRef: MatDialogRef<AlmacenCreateUpdateComponent>,
    private fb: FormBuilder,
    private wareHousesService: WareHouseService,
    private firestore: AngularFirestore,
    private storageService: StorageService
    ){}

    
  ngOnInit() {

    if(this.storageService.getSession("$key")!=null){
      this.almacenWeb = this.storageService.getSession("mydata")     
    }

    if (this.defaults) {
      this.mode = 'update';
    } else {
      this.defaults = {} as WareHouse;
    }

    const newId = this.firestore.createId();
    this.storageService.setSession("mykey", newId)

    this.form = this.fb.group({
      
      $key: newId,
      //id: [AlmacenCreateUpdateComponent.id++],     
      nombre: [this.defaults.nombre || ''],         
      id: [this.defaults.id || ''],
     

    });

    
  }

  save(wareHouseForm: NgForm) {
    if (this.mode === 'create') {
      this.addWareHouse(wareHouseForm);
    } else if (this.mode === 'update') {
      this.updateWareHouse();
    }
  }

	addWareHouse(wareHouseForm: NgForm) {
    this.wareHousesService.postWareHouse(wareHouseForm.value);
    this.createAlmacen();
    alert("Almacen guardado")

  }

  updateWareHouse(){
    if(this.storageService.getSession("$key")!=null){
      //var value = new AddEstablishment
        var values = { 
          //id:  this.almacenWeb.id,
          nombre: this.almacenWeb.nombre,    
          id: this.almacenWeb.id,
     
        }

        const almacen = this.form.value;  
        almacen.nombre = String(values.nombre)
        almacen.id = String(values.id)
        this.wareHousesService.updateWareHouse(values)
        this.updateAlmacen()
        console.log('print value::'+ JSON.stringify(almacen));
      }else{
        console.log('no entro')
      }
  }
  

  createAlmacen() {
    const almacen = this.form.value;

    if (!almacen.imageSrc) {
      almacen.imageSrc = 'assets/img/avatars/1.jpg';
    }

    this.dialogRef.close(almacen);
  }

  updateAlmacen() {
    const almacen = this.form.value;
    almacen.id = this.defaults.id;

    this.dialogRef.close(almacen);
  }

  isCreateMode() {
    return this.mode === 'create';
  }

  isUpdateMode() {
    return this.mode === 'update';
  }
}
